<template>
  <div>
    <div class="title">List of all Objects</div>
    <div v-for="category in sorted_categories" :key="category">
      <button class="button block" @click="show_related_objects[category] ^= true"><strong>{{ category }}</strong></button>
      <div v-show="show_related_objects[category]" v-for="obj in object_categories[category]" :key="obj">
        <router-link :to="'/docs/' + documentation_data[obj].n">
        {{ documentation_data[obj].n }}
        </router-link>
      </div>
      <br>
    </div>
  </div>
</template>

<script>
export default {
  name: "DocObjectList",
  props: {
    documentation_data: {
      type: Object,
      required: true,
    },
    object_categories: {
      type: Object,
      required: true,
    },
  },
  data() {
    return { 
      sorted_categories: Object.keys(this.object_categories).sort(), 
      show_related_objects: {},
    };
  },
  created() {
    window.scrollTo(0, 0);
    let keys_array = Object.keys(this.object_categories);
    for(let i = 0; i < keys_array.length; i++) {
      this.show_related_objects[keys_array[i]] = false;
    }
  }
};
</script>