<template>
  <div>
    <div class="title">List of all Global Constants</div>
    <div class="table-container">
      <table class="table is-striped">
        <thead>
          <tr>
            <th>Name</th>
            <th>Data Type</th>
            <th>Default Value</th>
            <th>Description</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="macro in macro_names" :key="macro" :id="macro" :class="{ 'has-background-black': $route.params.name == macro, 'has-text-white': $route.params.name == macro }">
            <td>{{ documentation_data[macro].n }}</td>
            <td>{{ documentation_data[macro].t }}</td>
            <td>{{ documentation_data[macro].v }}</td>
            <td><div class="description" v-html="documentation_data[macro].d"/></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: "DocMacro",
  props: {
    documentation_data: {
      type: Object,
      required: true,
    },
    macro_names: {
      type: Array,
      required: true,
    }
  },
  mounted() {
    try {
      if (this.$route.params.name != "global_constants") {
        document.getElementById(this.$route.params.name).scrollIntoView({block: "center"});
      } else {
        window.scrollTo(0, 0);
      }
    } catch(err) {
      console.log(err);
    }
  }
};
</script>