<template>
  <div>
    <div class="title">List of all Functions</div>
    <div v-for="category in sorted_categories" :key="category">
      <button class="button block" @click="show_related_functions[category] ^= true"><strong>{{ category }}</strong></button>
      <div v-show="show_related_functions[category]" v-for="func in function_categories[category]" :key="func">
        <router-link :to="'/docs/' + documentation_data[func].n">
          {{ documentation_data[func].n }}
        </router-link>
      </div>
      <br>
    </div>
  </div>
</template>

<script>
export default {
  name: "DocFunctionsList",
  props: {
    documentation_data: {
      type: Object,
      required: true,
    },
    function_categories: {
      type: Object,
      required: true,
    },
  },
  data() {
    return { 
      sorted_categories: Object.keys(this.function_categories).sort(), 
      show_related_functions: {},
      };
  },
  created() {
    window.scrollTo(0, 0);
    let keys_array = Object.keys(this.function_categories);
    for(let i = 0; i < keys_array.length; i++) {
      this.show_related_functions[keys_array[i]] = false;
    }
  }
};
</script>