      <section class="section">
        <div class="q">What is "Platform Fighter Engine"?</div>
        <div class="a">It is a GameMaker Studio 2 project that can be easily modified to make games in the platform fighter genre.</div>
        
        <div class="q">What game engine does it work for?</div>
        <div class="a">Currently, it only works on GMS2 (2.3+), so older versions of GameMaker or any other game development engines (Unity, Unreal, Godot, etc.) will NOT be compatible. There are no plans to officially port PFE to any other engine.</div>
        
        <div class="q">Do I have to know how to code to use it?</div>
        <div class="a">Somewhat. The engine comes with pre-made examples of characters and attacks. You can swap out sprites or change values of the pre-existing attacks without any prior code knowledge. If you want more freedom, you will need to learn GML.</div>
        
        <div class="q">How much does the engine cost?</div>
        <div class="a">The full release of PFE costs $30. GMS2 has a free trial, but it cannot create executables unless you purchase a license. All previously available beta versions of PFE can still be downloaded for free on itch.io page, but do not have as many features.</div>
        
        <div class="q">Does PFE have online play?</div>
        <div class="a">Yes, but if you want to add quickplay / random matchmaking you will need to host your own public matchmaking server.</div>
        
        <div class="q">Up to what ping can PFE support?</div>
        <div class="a">The quality of online matches can be affected by more than just ping - packet loss, graphics card settings, the set input delay, etc. - so we recommend you try out the demo first to make sure the game runs on your setup. We recommend playing on less than 150ms ping.</div>
        
        <div class="q">What platforms does PFE run on?</div>
        <div class="a">PFE only works on Windows, and there are no plans to officially port it to any other platforms.</div>
        
        <div class="q">How does PFE compare to Rivals Workshop or Fraymakers?</div>
        <div class="a">PFE gives you full control over the entire game. However, this means content made in PFE may not be compatible with other content people make. PFE is intended to be used to make standalone games, and there is no central place to share content for PFE.</div>
        
        <div class="q">Can I sell what I make with PFE?</div>
        <div class="a">Yes, you can make commercial games with PFE, as long as you don't share the source code. You can read the full license <router-link to="/about">here</router-link>.</div>
        
        <div class="q">Why does the project in GMS2 run slower than the demo executable?</div>
        <div class="a">The demo is compiled using YYC, which converts GML code to C++, allowing it to run much faster. Check out the <a href="https://help.yoyogames.com/hc/en-us/articles/235186048-Setting-Up-For-Windows" target="_blank" rel="noopener">Official YYC Guide</a> to learn how to use YYC.</div>
        
        <div class="q">Can I make a singleplayer story mode in PFE?</div>
        <div class="a">It's possible, but there is no built-in support for singleplayer modes.</div>
        
        <div class="q">How do I update to the current version of PFE from an older version?</div>
        <div class="a">Unfortunately, there is no easy way to move a project between versions of PFE. We recommend you keep track of all the changes you make using a source control program, and then port only those changes to the newer version.</div>
        
        <div class="q">Why is the game running slow on my computer?</div>
        <div class="a">This could be due to a number of reasons. Check your graphics card settings and make sure the refresh rate of your monitor isn't above 60. If you're using a laptop, set it to "best performance". On large monitors, use fullscreen mode instead of resizing the window. If the game is still not running at 60fps, try turning on "Performance Mode" or "Disable Shaders" from the in-game options.</div>
        
        <div class="q">How can I get my controller to work with PFE?</div>
        <div class="a">Only Xbox controllers and keyboards are guaranteed to work with PFE. If you are having issues with a different controller that cannot be fixed through the in-game settings, try running the game through Steam in Big Picture Mode, and setting up the controller with Steam's menu.</div>

        <div class="q">Can I make the engine 3D?</div>
        <div class="a">Unless you are an expert at using 3D in GMS2, no.</div>
