<template>
  <div class="section">
    <div v-if="$route.params.name == 'global_constants'">
      <DocMacro :documentation_data="documentation_data" :macro_names="macro_names"/>
    </div>
    <div v-else-if="$route.params.name == 'global_settings'">
      <DocSetting :documentation_data="documentation_data" :setting_names="setting_names"/>
    </div>
    <div v-else-if="$route.params.name == 'functions'">
      <DocFunctionsList :documentation_data="documentation_data" :function_categories="function_categories"/>
    </div>
    <div v-else-if="$route.params.name == 'objects'">
      <DocObjectsList :documentation_data="documentation_data" :object_categories="object_categories"/>
    </div>
    <div v-else-if="!documentation_data[$route.params.name]" class="has-text-danger">
      The page cannot be found!
    </div>
    <div v-else-if="documentation_data[$route.params.name].i == 'f'">
      <DocFunction :documentation_data="documentation_data" :function_categories="function_categories"/>
    </div>
    <div v-else-if="documentation_data[$route.params.name].i == 'o'">
      <DocObject :documentation_data="documentation_data"/>
    </div>
    <div v-else-if="documentation_data[$route.params.name].i == 'm'">
      <DocMacro :documentation_data="documentation_data" :macro_names="macro_names"/>
    </div>
    <div v-else-if="documentation_data[$route.params.name].i == 's'">
      <DocSetting :documentation_data="documentation_data" :setting_names="setting_names"/>
    </div>
    <div v-else class="has-text-danger">
      Error: The given page does not have a recognized type!
    </div>
  </div>
</template>

<script>
import CompDocFunction from '@/components/DocFunction.vue'
import CompDocObject from '@/components/DocObject.vue'
import CompDocMacro from '@/components/DocMacro.vue'
import CompDocSetting from '@/components/DocSetting.vue'
import CompDocFunctionsList from '@/components/DocFunctionsList.vue'
import CompDocObjectsList from '@/components/DocObjectsList.vue'

export default {
  name: "Doc",
  props: {
    documentation_data: {
      type: Object,
      required: true,
    },
    macro_names: {
      type: Array,
      required: true,
    },
    function_categories: {
      type: Object,
      required: true,
    },
    setting_names: {
      type: Array,
      required: true,
    },
    object_categories: {
      type: Object,
      required: true,
    }
  },
  components: {
    DocFunction: CompDocFunction,
    DocMacro: CompDocMacro,
    DocFunctionsList: CompDocFunctionsList,
    DocSetting: CompDocSetting,
    DocObject: CompDocObject,
    DocObjectsList: CompDocObjectsList,
  },
};
</script>