<template>
  <div>
    <div class="title">List of all Global Settings</div>
    <div class="table-container">
      <table class="table is-fullwidth is-striped">
        <thead>
          <tr>
            <th>Name</th>
            <th>Data Type</th>
            <th>Default Value</th>
            <th>Description</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="setting in setting_names" :key="setting" :id="setting" :class="{ 'has-background-black': $route.params.name == setting, 'has-text-white': $route.params.name == setting }">
            <td><span class="italics">{{ documentation_data[setting].s }}</span>{{ "." + documentation_data[setting].n }}</td>
            <td>{{ documentation_data[setting].t }}</td>
            <td>{{ documentation_data[setting].v }}</td>
            <td><div class="description" v-html="documentation_data[setting].d"/></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: "DocSetting",
  props: {
    documentation_data: {
      type: Object,
      required: true,
    },
    setting_names: {
      type: Array,
      required: true,
    }
  },
  mounted() {
    try {
      if (this.$route.params.name != "global_settings") {
        document.getElementById(this.$route.params.name).scrollIntoView({block: "center"});
      } else {
        window.scrollTo(0, 0);
      }
    } catch(err) {
      console.log(err);
    }
  }
};
</script>

<style scoped>
.italics {
  font-style: italic;
}
</style>